///////////		RESPONSIVE MIXINS	//////////////
@mixin mobile {
  @media screen and (max-width:640px) {
    @content;
  }
}
@mixin small {
  @media screen and (max-width:320px) and (min-width:0px) {
    @content;
  }
}

@mixin tab {
  @media screen and (max-width:1024px) {
    @content;
  }
}
@mixin xlarge {
  @media screen and (min-width:1441px) {
    @content;
  }
}
@mixin snormal {
  @media (min-width:1025px) and (max-width:14401px) {
    @content;
  }
}
@mixin mobile-portrait {
  @media screen and (max-device-width : 640px) and (orientation : portrait) {
    @content;
  }
}
@mixin mobile-landscape {
  @media screen and (max-device-width : 768px) and (orientation : landscape) {
    @content;
  }
}
@mixin tab-portrait {
  @media only screen 	and (max-device-width : 768px) and (orientation : portrait) {
    @content;
  }
}
@mixin tab-landscape {
  @media only screen and (max-device-width : 1024px) and (orientation : landscape) {
    @content;
  }
}

@mixin vendor($name, $argument) {
  -webkit-#{$name}: #{$argument};
  -ms-#{$name}: #{$argument};
  -moz-#{$name}: #{$argument};
  -o-#{$name}: #{$argument};
  #{$name}: #{$argument};
}


////////////	Utility Mixins 		//////////////////

@mixin nav(){
  font-size: $nav-size;
  font-family: $nav-font;
  a {
    color: $nav-color;
  }
}
@mixin placeholder {
  ::-webkit-input-placeholder {@content};
  :-moz-placeholder           {@content};
  ::-moz-placeholder          {@content};
  :-ms-input-placeholder      {@content}
}

@mixin box ($bg:transparent, $p:15px, $r:0px, $b:transparent, $s:transparent) {
  background-color: $bg;
  padding: $p;
  border-radius: $r;
  border: 1px solid $b;
  box-shadow: 2px 2px 2px $s, -2px 2px 2px $s;
}

@mixin round ($width, $border:transparent, $shadow:transparent) {
  width: $width;
  height: $width;
  border-radius: $width / 2;
  border: 1px solid $border;
  box-shadow: 1px 1px 1px $shadow;

}
@mixin button ($color, $bg, $border:transparent, $shadow:transparent, $radius:0px) {
  color: $color;
  background-color: $bg;
  border: 1px solid $border;
  box-shadow: 1px 2px 2px $shadow;
  border-radius: $radius;
}

@mixin line-clamp ($lineheight, $maxlines) {

  overflow: hidden;
  line-height: $lineheight;
  text-overflow: ellipsis;
  display: -webkit-box;
  max-height: $lineheight * $maxlines;      /* fallback */
  -webkit-line-clamp: $maxlines; /* number of lines to show */
  -webkit-box-orient: vertical;
}


///////////		Utility Classes 	///////////

div.container, section.container {
  width: $container-width;
  margin: 25px auto;
}
section.wide {
  width: $full-width;
  margin: 25px 0;
}
.center {
  text-align: center;
}
.v-container {
  display: table;
}
.v-container .vac {
  display: table-cell;
  vertical-align: middle;

}
li {list-style: none;}
ul.inline {
  li {
    display: inline-block;
  }
}